<template>
  <div class="data-table-wrapper">
    <table
      class="page-datatable"
      v-if="dataList.length != 0"
    >
      <thead>
        <tr>
          <th>
            <span>Meting</span>
          </th>
          <th>
            <span>Datum</span>
          </th>
          <th>
            <span>Gedaan door</span>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="dataEntry in dataList"
          :key="dataEntry.id"
          @click="$emit('click', dataEntry.id)"
        >
          <td>
            <span>{{dataEntry.data().type}}<span v-if="dataEntry.data().label.length > 0">, {{dataEntry.data().label}}</span></span>
          </td>
          <td>
            <span>{{prettifyDate(dataEntry.data().date.seconds)}}</span>
          </td>
          <td>
            <span>{{dataEntry.data().author}}</span>
          </td>
        </tr>
      </tbody>
    </table>
    <p
      class="page-datatable-disclaimer"
      v-if="dataList.length == 0"
    >Er zijn geen metingen gevonden.</p>
  </div>
</template>

<script>
export default {
  name: "rooms-data-list",
  props: {
    dataList: {
      type: Array
    }
  },
  methods: {
    prettifyDate: function(milliseconds) {
      const currentDate = new Date(1970, 0, 1);
      currentDate.setSeconds(milliseconds);
      const months = [
        "januari",
        "februari",
        "maart",
        "april",
        "mei",
        "juni",
        "juli",
        "augustus",
        "september",
        "oktober",
        "november",
        "december"
      ];
      const day = currentDate.getDate();
      const monthIndex = currentDate.getMonth();
      const year = currentDate.getFullYear();
      return `${day} ${months[monthIndex]} ${year}`;
    }
  }
};
</script>

<style>
</style>



