<template>
  <div
    class="page-box"
    v-if="currentRoom"
  >
    <div class="page-box-title">
      <h3>
        <div class="label">Ruimte</div>
        {{currentRoom.data().roomName}}
      </h3>
      <div class="flex-divider"></div>
      <div class="page-box-title-buttons">
        <base-button
          type="secondary"
          @click="openEditModal"
        >
          <base-icon iconRef="edit" />
        </base-button>
        <base-button
          type="secondary"
          @click="closeCurrentRoom"
        >
          <base-icon iconRef="close" />
        </base-button>
      </div>
    </div>
    <div class="page-box-content values">
      <div
        class="value-group"
        v-if="currentRoom.data().description != ''"
      >
        <div class="label">Beschrijving</div>
        <div class="value">{{currentRoom.data().description}}</div>
      </div>
      <div class="value-row">
        <div class="value-group">
          <div class="label">Ruimtesoort</div>
          <div class="value">{{allRoomTypeOptions.find(roomType => roomType.id == currentRoom.data().type).label}}</div>
        </div>
        <div class="value-group">
          <div class="label">Oppervlakte</div>
          <div class="value">
            {{(currentRoom.data().width * currentRoom.data().length) | $noDecimals}} m<sup>2</sup>
          </div>
        </div>
        <div class="value-group">
          <div class="label">Inhoud</div>
          <div class="value">
            {{(currentRoom.data().width * currentRoom.data().length * currentRoom.data().height) | $noDecimals}} m<sup>3</sup>
          </div>
        </div>
        <!-- <div class="value-group">
          <div class="label">Gem. aantal personen</div>
          <div class="value">
            {{currentRoom.data().people.amount}}, {{($peopleTypes.find(peopleType => peopleType.value == currentRoom.data().people.type).label).toLowerCase()}}
          </div>
        </div> -->
        <div class="value-group">
          <div class="label">Interieur</div>
          <div class="value">
            {{$interiors.find(interior => interior.value == currentRoom.data().interior).label}}
          </div>
        </div>
        <div class="value-group">
          <div class="label">Extra wensen</div>
          <div class="value">
            {{$wishes.find(wish => wish.value == currentRoom.data().wishes).label}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    roomTypeOptions: function() {
      return this.$store.getters.getActiveAdvicevaluesList;
    },
    allRoomTypeOptions: function() {
      return this.$store.getters.getAdvicevaluesList;
    },
    currentRoom: function() {
      return this.$store.getters.getCurrentClientPage.room;
    }
  },
  methods: {
    closeCurrentRoom: function() {
      this.$router.push(`/project/${this.currentRoom.data().belongsTo}`);
    },
    openEditModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "room",
        modalType: "edit",
        shown: true
      });
    }
  }
};
</script>


